div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

html {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

body {
  margin: 0;
  padding: 0;
  width: 100%;
  border: 0;
  line-height: 1.3;
  vertical-align: baseline; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

ul li {
  margin: 0;
  padding: 0; }

ol {
  margin: 0;
  padding: 0; }

p {
  padding: 0; }

fieldset {
  border: none; }

button {
  cursor: pointer; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

blockquote, q {
  quotes: none; }

r
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  z-index: 0; }

body main {
  padding-top: 9px; }

.wrapper {
  width: 100%;
  margin: 0 auto; }

.mid-content {
  max-width: 700px;
  margin: 0 auto; }

.wrapper,
.row {
  *zoom: 1; }

.wrapper:before, .wrapper:after,
.row:before,
.row:after {
  content: "";
  display: table; }

.wrapper:after,
.row:after {
  clear: both; }

.row {
  padding: 0 1em; }
  .row [class*='col-'] {
    vertical-align: top; }

[class*='col-'] {
  float: left;
  padding: 1em;
  width: 100%;
  min-height: 1px; }

@media only screen and (min-width: 500px) {
  .wrapper {
    width: 95%;
    max-width: 90em; }
  .wrapper__footer {
    margin: 0 auto;
    max-width: 97%; }
  .col-1 {
    width: 8.33333%; }
  .col-2 {
    width: 16.66667%; }
  .col-3 {
    width: 25%; }
  .col-4 {
    width: 33.33333%; }
  .col-5 {
    width: 41.66667%; }
  .col-6 {
    width: 50%; }
  .col-7 {
    width: 58.33333%; }
  .col-8 {
    width: 66.66667%; }
  .col-9 {
    width: 75%; }
  .col-10 {
    width: 83.33333%; }
  .col-11 {
    width: 91.66667%; }
  .col-12 {
    width: 100%; } }

@media (max-width: 360px) {
  .mid-content {
    width: 100%;
    margin: 0 auto; } }

.gridDemo span {
  display: block;
  background: #CCCCCC;
  color: black;
  padding: 1em 0;
  text-align: center; }
